efinitions from the dictionary)\n            ")]),_vm._v(" "),_c('ol',[_c('li',[_vm._v("\"A way in which something is usually done.\"")]),_vm._v(" "),_c('li',[_vm._v("\"An agreement between states covering particular matters, especially one less formal than a treaty.\"")])]),_vm._v(" "),_c('p',[_vm._v("\n                From this definition : \"A way in which something is usually done.\" , we realize that \n                conventions will help us work together as a group on some activities because they are \n                an agreement, understood by many, on how things are usually done.\n                They should be accepted as a base for carrying out some work, but they should not be \n                enforced (the word \"usually\" by definition implies something flexible). The conventions \n                will help us to some degree, to feel more comfortable with our code base, find our\n                way fast when changes are required.\n            ")]),_vm._v(" "),_c('p',{staticClass:"articleSubTitle"},[_vm._v("\n                Why is development required, why are changes required?\n            ")]),_vm._v(" "),_c('div',{staticClass:"articleParagraph"},[_vm._v("\n                Preservation is in our nature as human beings. We are always seeking for new ways \n                of making our lives simpler. We drive a car instead of walking, we prefer to \n                manage machinery instead \n                of managing and working with people. Because of this, we have always tried to\n                find new ways in which things \"should\" be done to have higher satisfaction and \n                preservation levels.\n            ")]),_vm._v(" "),_c('div',{staticClass:"articleParagraph"},[_vm._v("\n                You noticed how things changed because of human nature? From \"usually done\" we went to \n                \"should be done\". We found a link between conventions and development/evolution.\n                It is in our nature to feel comfortable about things and of course software is no \n                exception.\n            ")]),_c('br'),_vm._v(" "),_c('p',{staticClass:"articleSubTitle"},[_vm._v("\n                Rules are meant to be broken (for the good of course)\n            ")]),_vm._v(" "),_c('div',{staticClass:"articleParagraph"},[_vm._v("\n                Having established \"A way in which something is usually done.\", next we have to adapt. \n                As developers, we constantly have to do that. Every day, we have new problems to solve.\n                New problems which may make us uncomfortable when following blindly the way in which\n                something is usually done. \n            ")]),_vm._v(" "),_c('div',{staticClass:"articleParagraph"},[_vm._v("\n                How to become comfortable with the new set of problems? \n            ")]),_vm._v(" "),_c('div',{staticClass:"articleParagraph"},[_vm._v("\n                Establish new conventions, better practices of course.\n            ")]),_vm._v(" "),_c('div',{staticClass:"articleParagraph"},[_vm._v("\n                We have been doing this since forever. Look how human languages have evolved, how \n                many new concepts we have created that we take them for granted now, like money or \n                the economy. We simply shared with one another the knowledge, we agreed \n                that it's improving right now our way of life and accepted it as a new convention. \n                We all agreed for example, in the case of money, that bartering will not cut it anymore,\n                it's too hard to work with, to keep track of the value of every good in order arrange\n                a fair deal. We have agreed to use a single unit to describe value. We have paved the \n                way for something simpler, more natural and scalable.\n            ")]),_c('br'),_vm._v(" "),_c('p',{staticClass:"articleSubTitle"},[_vm._v("\n                Who defines the proper conventions that we, as developers, should follow? \n            ")]),_vm._v(" "),_c('div',{staticClass:"articleParagraph"},[_vm._v("\n                We do, as a community, by sharing with others our ideas that we worked so hard on and\n                that we tried and tested and we realized that they improve development, not just by\n                the number of problems we can solve in a given amount of time but also the way we feel\n                about our code.\n            ")]),_vm._v(" "),_c('div',{staticClass:"articleParagraph"},[_vm._v("\n                This does not imply that we should follow blindly every set of rules the majority \n                decides. We should also strive to understand the current accepted ways of doing things \n                and come up with new ideas if we see any flaws or find a way to make things seem simpler,\n                more natural. \n            ")]),_vm._v(" "),_c('div',{staticClass:"articleParagraph"},[_vm._v("\n                Even if our new ideas, solutions are not accepted by the majority, part of them can \n                still be helpful, not just for others who can build an improved version of our solutions,\n                but for ourselves also, it is part of our evolution as software developers, engineers \n                that give shape to our world.\n            ")]),_vm._v(" "),_c('div',{staticClass:"articleParagraph"},[_vm._v("\n                We should not focus on aligning 100% to some practices imposed by a majority, or some\n                organization that tries to enforce them. We should strive to come up with better\n                practices and not force every solution to be implemented in the same way. We should\n                strive to evolve and find better ways of solving problems. We should run away from \n                organizations, groups that are not flexible, where teams stay aligned 100% with the \n                organization. These kind of groups, organizations lack the feel of evolution, and when\n                they do decide to follow new, better practices, the costs might be too high to apply \n                the changes (in the video linked below, at the end after minute 60, you can see an \n                example where a company, Knight Capital Group, lost more than 450 million when they \n                decided to update legacy code, 8 years old code. Shortly after this error, they \n                agreed to be acquired by another company). \n            ")]),_vm._v(" "),_c('div',{staticClass:"articleParagraph"},[_vm._v("\n                We should work with and follow people who want to apply better practices even if \n                they miss some of the requirements of a problem. They are the kind of people \n                who feel natural with their work, confident and can fit more details in their heads\n                about a problem or a solution."),_c('br'),_c('br'),_vm._v(" "),_c('img',{staticClass:"img-fluid mx-auto d-block",attrs:{"src":require("../../../../assets/conventionsDiagram.png"),"alt":"","id":"conventionsDiagram"}}),_c('br'),_c('br')]),_vm._v(" "),_c('div',{staticClass:"articleParagraph"},[_vm._v("\n                If a company is focused on following some practices strictly with less room for\n                flexibility, would you want to follow such an organization? Will that company be able \n                to keep up with progress when its priority is to enforce some standards for better \n                delivery frequency? Will that company last long on a market that is constantly changing?\n                I would answer no to all these questions. I know that companies still try to keep up \n                with progress, but sadly, many times is not because of their internal culture, \n                but because of our desire to evolve. \n            ")]),_vm._v(" "),_c('div',{staticClass:"articleParagraph"},[_vm._v("\n                \"Our standardized monthly scrum performance report shows your delivery rate is below\n                average\". \n            ")]),_vm._v(" "),_c('div',{staticClass:"articleParagraph"},[_vm._v("\n                These types of reports generally deny the existence of technical debt, \n                rule out flexibility, they enforce the organization standards. \n            ")]),_vm._v(" "),_c('div',{staticClass:"articleParagraph"},[_vm._v("\n                I do agree that there must be deadlines, reasonable ones, but these kind of\n                performance reports that focus on delivery often do not take into consideration if \n                the code is maintainable, robust and if the ability to change comes natural. Many \n                problems we are trying to solve are different and often when diving in the details, \n                more problems emerge, problems which you may not notice if you are forced by some \n                organization's standards.\n            ")]),_c('br'),_vm._v(" "),_c('p',{staticClass:"articleSubTitle"},[_vm._v("\n                Conclusion\n            ")]),_vm._v(" "),_c('div',{staticClass:"articleParagraph"},[_c('b',[_c('i',[_vm._v("Use the conventions as a root for evolution.")])]),_c('br')]),_vm._v(" "),_c('div',{staticClass:"articleParagraph"},[_vm._v("\n                I think we should follow proper conventions, but we should also leave \n                room for flexibility and strive towards evolution, natural development practices, \n                a more robust codebase. \n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                The link I promised earlier. It's a very interesting presentation about simplicity\n                in software development. You can view it "),_c('a',{attrs:{"href":"https://www.youtube.com/watch?v=B3b4tremI5o","target":"_blank"}},[_vm._v("here")]),_vm._v(".\n            ")])])])])}]
